      h2 3.5.	Registro y solución de requerimientos de los usuarios
   
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p El registro y la atención de los requerimientos de los usuarios durante un recorrido turístico constituyen una herramienta esencial para garantizar la calidad del servicio, fortalecer la confianza y fomentar la mejora continua. En el contexto del turismo étnico y sostenible, este proceso debe realizarse con un enfoque intercultural, empático y transparente, que respete tanto las necesidades del visitante como las prácticas y dinámicas de las comunidades anfitrionas.
      p De acuerdo con la Norma Técnica Sectorial Colombiana NTS–TS 003 (ICONTEC, 2018), los prestadores de servicios turísticos deben implementar mecanismos de registro y seguimiento de observaciones, quejas, sugerencias o felicitaciones de los clientes, como parte del Sistema de Gestión de la Calidad. En entornos comunitarios, este registro puede adaptarse mediante herramientas participativas, tales como bitácoras de campo, entrevistas orales, o espacios de diálogo colectivo al finalizar el recorrido. Estas estrategias no solo fortalecen la retroalimentación, sino que promueven la comunicación horizontal entre visitantes, guías y comunidades locales, consolidando un modelo de gestión turística inclusivo y corresponsable.
      p En la siguiente tabla se pueden apreciar las acciones desarrollar frente al registro y la solución de requerimientos de los usuarios:

    .row.justify-content-center(data-aos="fade-left")
