    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p Algunos visitantes pueden sentirse incómodos al expresar quejas de manera directa, especialmente en contextos culturales donde la confrontación abierta no es habitual o se considera inapropiada.
      p Por ello, es recomendable que el guía o facilitador emplee técnicas de observación atenta, formule preguntas abiertas y genere espacios de diálogo basados en la confianza, con el fin de identificar posibles insatisfacciones o tensiones durante el recorrido.
      p En comunidades donde los conflictos se gestionan de forma indirecta o mediante mediadores, el guía turístico debe asumir el rol de puente cultural sensible, capaz de interpretar no solo las palabras, sino también los gestos, los silencios y los significados implícitos que surgen en la interacción.
      

